import axios from "axios";
import store from "../store";
import { getQueryString, rID } from "../utils";
import { isSocialWall, isTagbox, isTagShop } from "./api";
import { DATA_LOADER, FILTER_DATA, THEME_DATA } from "../reducers/reducersKeys";
import HttpClient from "./http-client";
import { getAttributes, isUpdateFrom } from "./commonAction";

var call;
export let dataRegion;
 
const getHashTagParam = (rID) => {
    const parsed = window.location.href;
    const updateContainerID = rID ? `#co_${rID}` : ".taggbox";
    let shopParams = {};
    let headerTags = {};
    
    const param = (key) => getQueryString(parsed, key);
    const region = param('region')
    const web = param('website')
    const tag = param('tags')

    if (isTagShop && (/.*(test\.tagshop\.ai\/w\/|widget-test\.tagshop\.ai|widget\.tagshop\.ai).*/.test(window.location.host) || web || region)) {
        shopParams = param('wall') ? {} : { Website: 1 };
        if (region) dataRegion = region;
    }
        
    if (isSocialWall && param('wallKey')) shopParams.wallKey = param('wallKey');
         headerTags = tag
        ? {
            tag: tag.includes("true") ? 1 : 0,
            keywords: decodeURIComponent(param('data-keywords') || param('keywords') || '')
            }
        : web
        ? { Website: 1, ...(dataRegion && { region: dataRegion }) }
        : (() => {
            const taggbox = document.querySelector(updateContainerID);
            if (!taggbox) return {};

            const { keywords, source, region, tags } = taggbox.dataset;

            return {
                ...(tags && {
                tag: tags.includes("true") ? 1 : 0,
                keywords: decodeURIComponent(keywords || '')
                }),
                ...(isTagShop && {
                Website: source ? 1 : undefined,
                region: region || undefined
                })
            };
            })();

    return { headers: { ...shopParams, ...headerTags, ...isUpdateFrom }};
}

export const filterPostDataAppendWebFilter = (
    wallID,
    timeStamp,
    postCount,
    networkId,
    after,
    heightEvent,
    tagName
) => (dispatch) => {
    const { wall, postData: dataPost } = store.getState().appData;
    const bestFit = wall?.ThemeInfo?.id === 4 && wall.ThemeStyle?.row === 1;

    const allKeys = Object.keys(dataPost?.filteredDataObject || {});
    const filterCount = allKeys.reduce(
        (count, key) =>
            count + (networkId === 0 ? (dataPost.filteredDataObject[key]?.length || 0) : 0),
        dataPost?.filteredDataObject[networkId]?.length || 0
    );

    const afterData = dataPost.appendData.after;

    const updateHeader = getHashTagParam(rID, wallID) || { headers: {} };
    const { headers } = updateHeader;

    headers.tag = tagName ? 1 : headers.tag || undefined;
    headers.keywords = tagName || headers.keywords || undefined;
    headers.action = 1;

    // Prepare default payload
    const defaultPayload = {
        appendData: {
            after: afterData,
            networkID: networkId,
            heightEvent,
            tagName,
        },
        filterNetworkId: networkId,
        newData: [],
        hasMoreData: {
            [networkId]: { hasMoreData: true, after: afterData },
        },
        tagName,
    };

    if (filterCount < postCount && filterCount !== networkId) {
        if (call) call.cancel();
        call = axios.CancelToken.source();
        updateHeader.cancelToken = call.token;

        dispatch({ type: DATA_LOADER, payload: true });

        const baseUrl = `${THEME_DATA}${wallID}/${timeStamp}/${postCount}/${networkId}/1/${after}`;
        const updateUrl = isTagShop ? `${baseUrl}/${getAttributes(rID)?.mUrl || ""}` : baseUrl;
        const urlToAccess = isTagbox ? `${THEME_DATA}${wallID}/${postCount}/${networkId}/${after}` : updateUrl;

        new HttpClient()
            .get(urlToAccess, updateHeader)
            .then((apiResponse) => {
                const response = isTagbox && !["62229", 62229].includes(wallID)
                    ? { data: JSON.parse([]) } // Replace `[]` with decompressed data if needed.
                    : apiResponse;

                dispatch({
                    type: FILTER_DATA,
                    payload: {
                        ...defaultPayload,
                        newData: response.data.data,
                        hasMoreData: {
                            [networkId]: {
                                hasMoreData:
                                    bestFit && Object.keys(response.data.data).length < postCount
                                        ? false
                                        : response.data.hasPost === 1,
                                after: response.data.after,
                            },
                        },
                        isFilter: true,
                    },
                });
            })
            .catch(() => {
                dispatch({ type: DATA_LOADER, payload: false });
            });
    } else {
        dispatch({
            type: FILTER_DATA,
            payload: defaultPayload,
        });
    }
};